import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',

    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path : '/cart',
    name : 'cart',
    component: () => import('../views/client/C_CartList.vue')
  },
  {
    path : '/ordPlaced',
    name : 'ordPlaced',
    component: () => import('../views/client/C_OrdPlaced.vue')
  },
  {
    path: '/main',
    name: 'main',
    component: () => import('../views/client/DJ_C_Main.vue')
  },
  {
    path:'/shoplist',
    name:'shoplist',
    component:() => import('../views/client/DJ_C_CateShop.vue')
  },
  // {
  //   path: '/shoplist/:categories',
  //   name: 'cateShoplist',
  //   component: () => import('../views/client/DJ_C_CateShop.vue')
  // },
]
const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
